    height: 100%;
  }
  .circle-carousel .slide {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -140px;
    color: #A84F9A;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    opacity: 0;
  }
  .circle-carousel .slide.active {
    z-index: 1;
    opacity: 1;
  }
  .circle-carousel .pagination {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 1;
    width: 90%;
    height: 90%;
    border-radius: 50%;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  .circle-carousel .pagination .dot {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    pointer-events: auto;
    -webkit-transition: .3s;
    transition: .3s;
  }
  .circle-carousel .pagination .dot:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    cursor: pointer;
    color: black;
  }
  .circle-carousel .pagination .item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .circle-carousel .pagination .item.active .dot {
    color: black;
    -webkit-transform: scale(2);
            transform: scale(2);
    left: 0;
    right: 0;
    margin: auto;
    top: -50px;
  }
  .dot span img{
    max-width: 110px;
    max-height: 180px;
  }
